.landing-container {
  /* background-image: url('../../assets/backgrounds/Splash_Bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; */
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.astronaut-logo {
  background-image: url('../../assets/icons/Astronaut_Icon.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 80vh;
  width: 80vh;
}

.slogan-wrapper {
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-weight: bold;
  z-index: 2;
}

#sbuhacks-title {
  font-size: 4rem;
  color: #ffffff;
  margin: 0;
  font-family: 'Goldman', cursive;
}

#sbuhacks-year {
  font-size: 6rem;
  color: #ffffff;
  margin: 0;
  font-family: 'Goldman', cursive;
}

#landing-sponsor {
  font-size: 3rem;
}

#sbuhacks-date,
.sbuhacks-slogan,
#landing-sponsor {
  margin: 0;
}

.landing-sub p {
  font-size:  2.5vw;
  color: #ffffff;
}

.landing-sub a {
  color: #ffffff;
}

.planet {
  position: absolute;
  display: flex;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  transition: all 0.8s ease-in-out;
  cursor: pointer;
  z-index: 2;

  background-size: contain;
  background-repeat: no-repeat;
}

.planet .link {
  width: 100%;
}

.planet:nth-child(1) {
  background-image: url('../../assets/icons/About_Icon.png');
  width: 9vw;
  height: 9vw;
  padding-right: 50px;
  padding-bottom: 30px;
  top: 0;
  left: 0;
}

.planet:nth-child(2) {
  background-image: url('../../assets/icons/FAQ_Icon.png');
  width: 5.5vw;
  height: 5.5vw;
  top: 25%;
  left: 1%;
}

.planet:nth-child(3) {
  background-image: url('../../assets/icons/Schedule_Icon.png');
  width: 10vw;
  height: 10vw;
  top: 34%;
  left: 8%;
}

.planet:nth-child(4) {
  background-image: url('../../assets/icons/Sponsors_Icon.png');
  width: 9vw;
  height: 9vw;
  top: 55%;
  left: 0%;
}

.planet:nth-child(5) {
  background-image: url('../../assets/icons/Contact_Icon.png');
  width: 11vw;
  height: 11vw;
  top: 80%;
  left: 5%;
}

.planet:nth-child(6) {
  background-image: url('../../assets/icons/Asteroid_Icon.png');
  width: 5.5vw;
  height: 5.5vw;
  top: 48%;
  left: 10%;
  transform: rotate(160deg);
}

.planet:nth-child(7) {
  background-image: url('../../assets/icons/Asteroid_Icon.png');
  width: 3vw;
  height: 3vw;
  top: 78%;
  left: 9%;
  transform: rotate(30deg);
}

.planet:nth-child(8) {
  background-image: url('../../assets/icons/Asteroid2_Icon.png');
  width: 4.5vw;
  height: 4.5vw;
  top: 95%;
  left: 2.5%;
}

.planet-title {
  background-color: transparent;
  color: #ffffff;
  border: none;
  font-size: 1.5vw;
  cursor: pointer;
  font-family: 'Goldman', cursive;
  text-shadow: 3.5px 3.5px #02111b;
  width: 100%;
  height: 100%;
}

.ombre-stripes-1 {
  background-image: url('../../assets/icons/Waves.png');
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  z-index: 1;
  width: 50vw;
  height: 60vw;
  top: -20vw;
  right: 0;
  transform: scale(-1, -1);
  transform: rotate(180deg);
  transform: translateX(40%);
}
.radar-signal {
  background-image: url('../../assets/icons/Radar_Signal.png');
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  z-index: 1;
  width: 100vw;
  height: 60vh;
  top: 80vh;
  left: calc(-50vw - 30%);
  transform: scale(-1, 1);
}

.globe-outline {
  background-image: url('../../assets/icons/Globe_Outline.PNG');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  width: 100vw;
  height: 30vh;
  top: 80vh;
  transform: scale(-1, 1);
}

.register-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 120px;
  font-weight: bold;
  font-size: 20px;
  color: #ffffff;
  cursor: pointer;
  z-index: 2;
}

@media screen and (max-width: 2000px) {
  .ombre-stripes-2 {
    top: 55vw;
  }
}

@media screen and (max-width: 1600px) {
  .slogan-wrapper {
    width: 850px;
  }

  #sbuhacks-title {
    font-size: 6rem;
  }

  #sbuhacks-year {
    font-size: 4.5rem;
  }

  #landing-sponsor {
    font-size: 2rem;
  }

  .ombre-stripes-1,
  .ombre-stripes-2 {
    width: 40vw;
    height: 48vw;
  }

  .ombre-stripes-2 {
    top: 50vh
  }

  .landing-sub {
    font-size: 1.5vw;
  }

  .planet:nth-child(1) {
    width: 120px;
    height: 120px;
  }

  .planet:nth-child(2) {
    width: 90px;
    height: 90px;
  }

  .planet:nth-child(3) {
    width: 140px;
    height: 140px;
  }

  .planet:nth-child(4) {
    width: 140px;
    height: 140px;
  }

  .planet:nth-child(5) {
    width: 170px;
    height: 170px;
  }

  .planet-title {
    font-size: 24px;
  }
}

@media screen and (max-width: 1200px) {
  .ombre-stripes-1,
  .ombre-stripes-2 {
    width: 600px;
    height: 720px;
  }

  .ombre-stripes-1 {
    transform: translateX(360px);
  }

  .ombre-stripes-2 {
    top: 35vw;
    transform: translateX(-10vw);
  }

  .landing-container {
    flex-direction: column;
  }

  .astronaut-logo {
    margin-top: 50px;
  }

  .globe-outline {
    display: none;
  }
}

@media screen and (max-width: 916px) {
  .slogan-wrapper {
    width: 800px;
  }

  #sbuhacks-title {
    font-size: 4.5rem;
  }

  #sbuhacks-year {
    font-size: 3.5rem;
  }
}

@media screen and (max-width: 798px) {
  .landing-sub p {
    font-size: 4vw;
  }

  .ombre-stripes-1 {
    transform: translate(400px, -100px);
  }

  .ombre-stripes-2 {
    top: 70vh;
    left: -45vw;
  }

  .astronaut-logo {
    height: 50vh;
    width: 50vh;
  }
}

@media screen and (max-width: 700px) {
  .slogan-wrapper {
    width: 600px;
  }

  #sbuhacks-title {
    font-size: 3rem;
    text-shadow: none;
  }

  #sbuhacks-year {
    font-size: 2rem;
    text-shadow: none;
  }

  #landing-sponsor {
    font-size: 4vw;
  }

  .landing-sub p {
    font-size: 1.75rem;
  }

  .register-button {
    bottom: 2%;
  }

  .ombre-stripes-1,
  .ombre-stripes-2 {
    width: 400px;
    height: 480px;
  }

  .ombre-stripes-1 {
    transform: translate(220px, -50px);
  }

  .ombre-stripes-2 {
    top: 60vh;
    transform: translate(0, 10vw);
  }
}

@media screen and (max-width: 320px) {
  .slogan-wrapper {
    padding: 0 0 70px 10px;
  }

  #landing-sponsor {
    font-size: 5vw;
  }

  .ombre-stripes-2 {
    top: 50vh;
  }
}